.presentacion {
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: 'Open Sans';
}

.span {
    font-family: 'Open Sans';
    font-size: 40px;
    font-weight: bold;
    color: white;
}

.container {
    display: flex;
    height: 50%;
}
.container-image {
    height: auto;
    width: 50%;
    display: flex;
    align-items: center;
    justify-content: flex-start;
}

.info-container {
    width: 100%;
    height: 100%;
    display: flex;
    
}

.info-container-left {
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.info-container-left p {
    
    font-family: 'Open Sans';
    font-weight: bold;
    display: flex;
    align-items: center;
    
}
.info-container-right {
    width: 50%;
    
}
.icons {
    font-size: 50px;
}
.info-container-right p {
    
    font-family: 'Open Sans';
    font-weight: bold;
    display: flex;
    align-items: center;
    
}
@media (max-width: 700px) {

    .info-container {
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        margin-top: 20px;
    }
    .container { 
        display: flex;
        flex-direction: column;
        align-items: center;
        
    }
    .container-image {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 30vh;
        width: 80% ;
        
    }
    
    
}